// ? ======= footer start ======= //

.footer {
    width: 100%;
    height: 363px;
    padding-top: 105px;
    // padding-bottom: 97px;
    background-color: var(--color-ecruWhite);
    display: flex;
    flex-direction: column;

    &__container {
        width: 100%;
        // height: 95px;
        display: flex;
        justify-content: space-between;
        align-items: flex-start;
    }

    &__nav {
        display: flex;
        margin-left: -120px;
    }

    &__item {
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: var(--color-black);
        transition: .5s;

        &:hover {
            transition: .5s;
            cursor: pointer;
            color: var(--color-gothic);
        }

        &:not(:last-child) {
            margin-right: 45px;
        }
    }

    &__link {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }

    &__link-contact {
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: var(--color-black);
    }

    &-cart__icon {
        width: 24px;
        height: 24px;
    }

    &__bottom {
        width: 100%;
        display: flex;
        justify-content: space-between;
        align-items: center;
        font-style: normal;
        font-weight: 400;
        font-size: 13px;
        letter-spacing: 0.02em;
        color: var(--color-black);
        // margin-top: 5px;
    }

    &__copyright {
        padding-top: 39px;
    }

    &__copyright-text {
        margin: 0;
    }

    &__copyright-text:not(:last-child) {
        margin-bottom: 10px;
    }

    &__list {
        margin-left: -365px;
        height: 113px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding-top: 10px;
    }

    &__social-list {
        display: flex;
    }

    &__social-item:not(:last-child) {
        margin-right: 12px;
    }

    &__icon {
        width: 23px;
        height: 23px;
    }

    &__item-link {
        font-style: normal;
        font-weight: 400;
        font-size: 13px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: var(--color-black);
    }

    &__payment {
        text-align: right;
        margin-top: 22px;
    }

    &__link-phone {
        margin-bottom: 9px;
    }

    &__social {
        height: 100px;
        display: flex;
        flex-direction: column;
        justify-content: flex-end;
    }
}