//? ======= gallery start =======//

.gallery {
    padding-top: 94px;
    padding-bottom: 235px;

    &__product-counter {
        margin: 0;
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: var(--color-gray2);
    }

    &__gallery {
        margin-top: 63px;
        margin-bottom: 61px;
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(3, 560px);
        grid-auto-rows: 560px;
        column-gap: 32px;
        row-gap: 65px;
        ;
    }

    &-gallery__card {
        width: 350px;
        height: 560px;
        margin: 0;
    }

    &-gallery__pic {
        max-height: 478px;
        background-color: var(--color-alto);
        position: relative;
    }

    &-gallery__background::before {
        position: absolute;
        content: '';
        width: 100%;
        height: 100%;
        background-color: rgba(110, 156, 159, 0.64);
        background-image: url(../../img/hand.svg);
        background-repeat: no-repeat;
        background-position: center center;
        opacity: 0;
        top: 0;
        left: 0;
        transition: .5s;
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &-gallery__background:hover::before {
        opacity: .65;
        transition: .5s;
    }

    &-gallery__background-link {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        display: block;
    }

    &-gallery__image {
        width: 100%;
        height: 100%;
        display: block;
        object-fit: cover;
    }

    &-gallery__signed {
        margin-top: 22px;
        text-align: center;
    }

    &-gallery__product {
        margin: 0;
        margin-bottom: 10px;
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: var(--color-black);
    }

    &-gallery__price {
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: var(--color-paleOyster);

        &--old {
            color: var(--color-dustyGray);
            margin-right: 10px;
        }
    }
}