.contacts-title {
    padding-top: 270px;
    padding-left: 5px;

    &__title {
        margin: 0;
        font-style: normal;
        font-weight: 500;
        font-size: 55px;
        line-height: 110%;
        letter-spacing: 0.02em;
        color: var(--color-black);
    }

    &__pagination {
        margin-top: 26px;
    }

    &__pagination-list {
        display: flex;
        justify-content: flex-start;

        span {
            margin: 0 10px;
        }
    }

    &__pagination-link {
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 140%;
        letter-spacing: 0.02em;
        // margin-right: 20px;
        color: var(--color-black);

        &--active {
            color: var(--color-gray1);
        }
    }
}