//? ======= header start =======//

.header {
    width: 100%;
    position: fixed;
    z-index: 22;

    &__container {
        padding-top: 38px;
        width: 100%;
        height: 95px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__nav {
        display: flex;
        margin-left: -33px;
    }

    &__item {
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: var(--color-black);
        transition: .5s;

        &:hover {
            transition: .5s;
            cursor: pointer;
            color: var(--color-gothic);
        }

        &:not(:last-child) {
            margin-right: 45px;
        }
    }

    &__link {
        display: flex;
        align-items: center;
    }

    &-phone__icon {
        width: 15px;
        height: 15px;
        margin-right: 10px;
        fill: var(--color-gothic);
    }

    &__link-contact {
        font-style: normal;
        font-weight: 400;
        font-size: 15px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: var(--color-black);
        margin-right: 74px;
    }

    &-cart__icon {
        width: 24px;
        height: 24px;
    }
}