//? ======= benefits start =======//

.benefits {
    padding-top: 128px;
    padding-bottom: 130px;

    &__title {
        margin: 0;
        margin-bottom: 92px;
        font-style: normal;
        font-weight: 500;
        font-size: 40px;
        line-height: 110%;
        letter-spacing: 0.02em;
        color: var(--color-black);
    }

    &__list {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-auto-rows: auto;
        column-gap: 29px;
    }

    &__item {
        font-style: normal;
        font-weight: 500;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: var(--color-black);
    }

    &__icon {
        width: 61px;
        height: 61px;

        use {
            fill: var(--color-black);
            stroke: var(--color-black);
        }
    }

    &__subtitle {
        margin: 36px 0 33px 0;
        font-size: 25px;
    }

    &__text {
        margin: 0;
        font-size: 17px;
    }
}