.filter {
    padding-top: 214px;

    &__tabs {
        display: flex;
        justify-content: flex-start;
        height: 61px;
    }

    &__tabs-item {
        height: 100%;

        &:not(:last-child) {
            margin-right: 9px;
        }
    }

    &__tabs-link {
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: var(--color-black);
        width: 100%;
        height: 100%;
        display: flex;
        align-items: center;
        padding: 0 3.8em;
        border: 1px solid var(--color-black);
        transition: .5s;

        &:hover {
            background-color: var(--color-black);
            color: var(--color-white);
            transition: .5s;
        }
    }
}