//? ======= history start =======//

.history {
    padding-top: 213px;
    padding-bottom: 130px;

    &__content {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__block {
        display: flex;
        justify-content: flex-start;
        align-items: flex-start;
    }

    &__pic {
        width: 40%;
        height: 547px;
    }

    &__description {
        width: 50%;
        height: 100%;
        margin-left: 92px;
        line-height: 145%;
        letter-spacing: 0.02em;
        font-weight: 500;
        font-style: normal;
        color: var(--color-black);
        padding-top: 120px;
    }

    &__title {
        margin: 0;
        font-size: 25px;
        margin-bottom: 53px;
    }

    &__text {
        margin: 0;
        font-size: 17px;

        &:not(:last-child) {
            margin-bottom: 30px;
        }
    }

    &__block-bottom {
        flex-direction: row-reverse;
        justify-content: space-between;
        margin-top: 130px;
    }

    &__description--bottom {
        width: 50%;
        height: 100%;
        margin-left: 0;
        line-height: 145%;
        letter-spacing: 0.02em;
        font-weight: 500;
        font-style: normal;
        color: var(--color-black);
        padding-top: 84px;
    }

    &__btn {
        width: 260px;
        height: 68px;
        border: none;
        display: flex;
        justify-content: center;
        align-items: center;
        font-style: normal;
        font-weight: 400;
        font-size: 17px;
        line-height: 138.9%;
        letter-spacing: 0.02em;
        color: var(--color-white);
        background-color: var(--color-gothic);
        transition: .5s;
        margin-top: 130px;

        &:hover {
            color: var(--color-gothic);
            background-color: var(--color-white);
            border: 1px solid var(--color-gothic);
            transition: .5s;
        }
    }
}