//? ======= contacts start =======//

.contacts {
    padding-top: 132px;

    &__list {
        display: flex;
        justify-content: flex-start;
    }

    &__item {
        display: flex;
        flex-direction: column;
        align-items: flex-start;
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: var(--color-black);

        &:nth-child(1) {
            margin-right: 124px;
        }

        &:nth-child(2) {
            margin-right: 98px;
        }
    }



    &__text {
        font-size: 20px;
        margin-top: 19px;
    }
}