//? ======= hero start =======//

.hero {
    margin-bottom: 40px;

    &__background {
        width: 45%;
        height: 826px;
        background-color: var(--color-ecruWhite);
        position: absolute;
        top: 0;
        right: 0;
    }

    &__container {
        display: flex;
        justify-content: space-between;
    }

    &__left {
        width: 50%;
        padding-top: 267px;
    }

    &__title {
        margin: 0;
        font-style: normal;
        font-weight: 500;
        font-size: 55px;
        line-height: 110%;
        letter-spacing: 0.02em;
        color: var(--color-black);
    }

    &__right {
        width: 45%;
        padding-top: 120px;
        padding-left: 90px;
        position: relative;
    }

    &__pic--large {
        width: 410px;
        height: 646px;
    }

    &__pic--left {
        width: 190px;
        height: 318px;
        position: absolute;
        bottom: -46px;
        left: -8%;
    }

    &__pic--right {
        width: 197px;
        height: 197px;
        position: absolute;
        bottom: 43.5%;
        left: 81.5%;
    }

    &__slider {
        width: 385px;
        margin: 0;
    }

    &__slide {
        display: flex;
        flex-direction: column;
        align-items: flex-end;
    }

    &__slide-text {
        margin: 0;
        margin-top: 44px;
        margin-bottom: 53px;
        text-align: left;
        font-style: normal;
        font-weight: 400;
        font-size: 20px;
        line-height: 140%;
        text-align: right;
        letter-spacing: 0.02em;
        color: var(--color-black);
    }

    &__button-wrap {
        width: 311px;
        height: 67px;
        display: flex;
        justify-content: space-between;
        background: rgba(110, 156, 159, 0.1);
    }

    &__wrap-svg {
        width: 67px;
        height: 67px;
        display: flex;
        justify-content: center;
        align-items: center;
    }

    &__arrow {
        width: 34px;
        height: 28px;
        transform: rotate(90deg);

        use {
            stroke: var(--color-gothic);
        }
    }

    &__link {
        width: 244px;
        height: 65px;
        display: flex;
        justify-content: center;
        align-items: center;
        font-style: normal;
        font-weight: 400;
        font-size: 17px;
        line-height: 138.9%;
        text-align: center;
        letter-spacing: 0.02em;
        color: var(--color-white);
        background-color: var(--color-gothic);
        transition: 0.5s;

        &:hover {
            color: var(--color-gothic);
            background-color: var(--color-white);
            border: 1px solid var(--color-gothic);
            transition: 0.5s;
        }
    }

    //? ======= swiper start =======//

    &__slider {
        height: 392px;
    }

    &__pagination {
        left: 134px !important
    }

    &__pagination>.swiper-pagination-bullet {
        background-color: var(--color-black);
        opacity: .36;
        width: 30px;
        height: 4px;
        border-radius: 0;
    }

    &__pagination>.swiper-pagination-bullet-active {
        opacity: 1;
    }
}