//? ======= collection start =======//

.collection {
    padding-top: 177px;

    &__container {
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    &__title {
        margin: 0 0 92px 0;
        font-style: normal;
        font-weight: 500;
        font-size: 40px;
        line-height: 110%;
        letter-spacing: 0.02em;
        color: var(--color-black);
        align-self: flex-start;
    }

    &__gallery {
        display: grid;
        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: 560px;
        column-gap: 32px;
        ;
    }

    &-gallery__card {
        width: 350px;
        height: 560px;
        margin: 0;
    }

    &-gallery__pic {
        max-height: 480px;
        background-color: var(--color-alto);
        position: relative;
    }

    &-gallery__background::before {
        position: absolute;
        content: '';
        width: 100%;
        height: 100%;
        background-color: rgba(110, 156, 159, 0.64);
        background-image: url(../../img/hand.svg);
        background-repeat: no-repeat;
        background-position: center center;
        opacity: 0;
        top: 0;
        left: 0;
        transition: .5s;
        cursor: pointer;
    }

    &-gallery__background:hover::before {
        opacity: .65;
        transition: .5s;
    }

    &-gallery__background-link {
        width: 30px;
        height: 30px;
        display: block;
    }

    &-gallery__image {
        width: 100%;
        height: 100%;
        display: block;
        object-fit: cover;
    }

    &-gallery__signed {
        margin-top: 22px;
        text-align: center;
    }

    &-gallery__product {
        margin: 0;
        margin-bottom: 10px;
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: var(--color-black);
    }

    &-gallery__price {
        font-style: normal;
        font-weight: 500;
        font-size: 15px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: var(--color-paleOyster);

        &--old {
            color: var(--color-dustyGray);
            margin-right: 10px;
        }
    }

    &-gallery__link {
        width: 243px;
        height: 68px;
        border: 1px solid var(--color-gothic);
        margin-top: 65px;
        text-align: center;
        display: flex;
        justify-content: center;
        align-items: center;
        font-style: normal;
        font-weight: 400;
        font-size: 17px;
        line-height: 138.9%;
        letter-spacing: 0.02em;
        color: var(--color-gothic);
        transition: .5s;

        &:hover {
            color: var(--color-white);
            background-color: var(--color-gothic);
            transition: .5s;
        }
    }
}