//? ======= contacts-form start =======//

.contacts-form {
    padding-top: 108px;
    padding-bottom: 129px;

    &__title {
        font-style: normal;
        font-weight: 500;
        font-size: 25px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: var(--color-black);
    }

    &__form {
        width: 437px;
        margin-bottom: 62px;
        display: flex;
        flex-direction: column;
        align-items: flex-start;
    }

    &__label {
        width: 350px;
        height: 75px;
        border-bottom: 1px solid var(--color-gray);
        display: flex;
        align-items: flex-end;
        padding-bottom: 4px;
    }

    &__input {
        border: none;
        width: 100%;
        height: 40px;
        padding: 0 20px 0 0;
        outline: none;
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: var(--color-gray);
    }

    &__message {
        width: 100%;
        height: 171px;
        border: none;
        border-bottom: 1px solid var(--color-gray);
        outline: none;
        padding-top: 35px;
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: var(--color-gray);
        margin-bottom: 35px;
    }

    &__submit {
        width: 189px;
        height: 68px;
        display: flex;
        justify-content: center;
        align-items: center;
        border: none;
        background-color: var(--color-gothic);
        font-style: normal;
        font-weight: 400;
        font-size: 17px;
        line-height: 138.9%;
        text-align: center;
        letter-spacing: 0.02em;
        color: var(--color-white);
        transition: .5s;

        &:hover {
            background-color: var(--color-white);
            color: var(--color-gothic);
            transition: .5s;
            border: 1px solid var(--color-gothic);
        }
    }

    &__sent {
        width: 437px;
        height: 84px;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: var(--color-ecruWhite);
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: var(--color-black);
    }
}