//? ======= product-card start =======//

.product-card {
    margin-top: 102px;

    &__container {
        display: flex;
        justify-content: space-between;
    }

    &__block-pic {
        width: 48.5%;
        height: 729px;
    }

    &__pic {
        width: 100%;
        height: 100%;
        object-fit: cover;
    }

    &__features {
        width: 51.5%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        padding-left: 74px;

        &-price {
            font-style: normal;
            font-weight: 500;
            font-size: 30px;
            line-height: 110%;
            letter-spacing: 0.02em;
            color: var(--color-dustyGray);
        }
    }

    &__price--old {
        color: var(--color-paleOyster);
        font-size: 41px;
        margin-right: 20px;
    }

    &__subtitle {
        margin: 64px 0 31px 0;
        font-style: normal;
        font-weight: 500;
        font-size: 20px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: var(--color-black);
    }

    &__size {
        display: flex;
        justify-content: flex-start;
    }

    &__size-item {
        width: 41px;
        height: 41px;
        border: 1px solid var(--color-black);
        display: flex;
        justify-content: center;
        align-items: center;
        font-style: normal;
        font-weight: 500;
        font-size: 17px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: var(--color-black);
        transition: .5s;

        &:hover {
            color: var(--color-white);
            background-color: var(--color-black);
            transition: .5s;
        }

        &:not(:last-child) {
            margin-right: 14px;
        }
    }

    &__color {
        display: flex;
        margin-bottom: 59px;
    }

    &__color-item {
        width: 41px;
        height: 41px;
        border-radius: 50%;
        transition: 0.5s;

        &:hover {
            border: 1px solid var(--color-black);
            transition: 0.5s;
        }

        &--brown {
            background-color: var(--color-hemp);
        }

        &--gray {
            background-color: var(--color-alto1);
        }

        &--pinc {
            background-color: var(--color-sweetPink);
        }

        &--orange {
            background-color: var(--color-peachOrange);
        }

        &:not(:last-child) {
            margin-right: 14px;
        }
    }

    &__choice {
        display: flex;
        justify-content: flex-start;

        &-caunter {
            width: 68px;
            height: 68px;
            margin-right: 11px;
            display: flex;
            justify-content: center;
            align-items: center;
            font-style: normal;
            font-weight: 400;
            font-size: 20px;
            line-height: 140%;
            letter-spacing: 0.02em;
            color: var(--color-black);
            border: 1px solid var(--color-alto);
        }

        &-card {
            width: 268px;
            height: 68px;
            border: none;
            display: flex;
            align-items: center;
            justify-content: center;
            font-style: normal;
            font-weight: 400;
            font-size: 17px;
            line-height: 138.9%;
            text-align: center;
            letter-spacing: 0.02em;
            color: var(--color-white);
            background-color: var(--color-gothic);
            transition: .5s;

            &:hover {
                color: var(--color-gothic);
                background-color: var(--color-white);
                border: 1px solid var(--color-gothic);
                transition: .5s;
            }
        }
    }
}