//? ======= command start =======//

.command {
    padding-top: 22px;
    padding-bottom: 117px;

    &__container {
        font-style: normal;
        font-weight: 500;
        font-size: 25px;
        line-height: 140%;
        letter-spacing: 0.02em;
        color: var(--color-black);
        width: 1200px;
        margin-left: auto;
        margin-right: auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }

    &__title {
        margin: 0 0 92px 0;
        font-size: 40px;
    }

    &__slider {
        width: 71%;
        padding-left: 39px;
    }

    &__content {
        width: 28%;
        padding-left: 33px;
        height: 345px;
    }

    &__subtitle {
        margin: 0 0 28px 0;
        font-size: 25px;
    }

    &__text {
        margin: 0 0 23px 0;
        font-size: 17px;
        line-height: 140%;
    }

    &__link {
        font-size: 17px;
        color: var(--color-gothic);
    }

    //? ======= swiper start =======//

    &__pagination>.swiper-pagination-bullet {
        background-color: var(--color-white);
        opacity: .36;
        width: 30px;
        height: 4px;
        border-radius: 0;
    }

    &__pagination>.swiper-pagination-bullet-active {
        opacity: 1;
    }

    &__button-prev::after {
        content: '';
    }

    &__button-next::after {
        content: '';
    }

    &__prev {
        width: 50px;
        height: 50px;

        use {
            width: 100%;
            height: 100%;
            stroke: var(--color-gray);
        }
    }
}